import { useEffect, useState } from "react";
const useScrollHeight = (ref) => {
    const [scrollTop, setScrollTop] = useState(0);
    let throttling = false;

    const handleScroll = () => {
    // throttling ë˜ì—ˆëŠ”ì§€ ë‚˜íƒ€ëƒ„.
    if (throttling) return;

        // ì½˜ì†” ë©”ì„¸ì§€ ì ê²Œ í•˜ê¸°ìœ„í•´ throttlingìœ¼ë¡œ ìµœì í™”í•œì…ˆ
        throttling = true;

        setTimeout(() => {
            setScrollTop(ref.current.scrollTop);
            throttling = false;
        }, 300);
    };

    // ì´ë²¤íŠ¸ë¦¬ìŠ¤ë„ˆ í•œ ë²ˆ ì‹¤í–‰í•˜ë©´ ì‚­ì œ
    useEffect(() => {
        if (!ref.current) return;
        ref?.current?.addEventListener('scroll', handleScroll, { passive: true });

        return () => {
            if (!ref.current) return;
            ref?.current?.removeEventListener('scroll', handleScroll);
        };
    }, [ref]);

    return scrollTop;
};

export default useScrollHeight;